// Change search icon on header input search
var header_search = document.getElementById('header-search');

if (header_search){
    header_search.addEventListener("mouseover", function (event) {
        document.querySelector('.search__container-black').classList.remove('hidden');
      });
      
      header_search.addEventListener("mouseout", function (event) {
        document.querySelector('.search__container-black').classList.add('hidden');
      });
      
      header_search.addEventListener("focus", function (event) {
        document.querySelector('.search__container-black').classList.add('showit');
      });
      
      header_search.addEventListener("focusout", function (event) {
        document.querySelector('.search__container-black').classList.remove('showit');
      
      
        if (header_search.value.length == 0) {
          header_search.value = "Search by Order #";
        }
      });
}


// set datepicker value to default date

var defaultDate = new Date();
var dd = String(defaultDate.getDate()).padStart(2, '0');
var mm = String(defaultDate.getMonth() + 1).padStart(2, '0'); //January is 0!
var yyyy = defaultDate.getFullYear();

defaultDate = mm + '/' + dd + '/' + yyyy;

$('.custom__datepicker').attr('placeholder', defaultDate);


// assign value on select
$('.custom__dropdown-options > ul li').click(function (e) {
    setSelectedListItem(e);
    closeSelectList(e);
});

// make select keyboard accessible
// TODO: needs work
// set constant values
const SPACEBAR_KEY_CODE = [0, 32];
const ENTER_KEY_CODE = 13;
const DOWN_ARROW_KEY_CODE = 40;
const UP_ARROW_KEY_CODE = 38;
const ESCAPE_KEY_CODE = 27;

$('.custom__dropdown-options > ul li').on('keydown', function (e) {
    // let parentSelect = e.target.offsetParent.getAttribute('id');
    // let parentSelectItems = e.target.nextElementSibling.children[0].children;

    switch (e.keyCode) {
        case ENTER_KEY_CODE:
            setSelectedListItem(e);
            closeSelectList(e);
            return;

        case DOWN_ARROW_KEY_CODE:
            focusNextListItem(DOWN_ARROW_KEY_CODE);
            return;

        case UP_ARROW_KEY_CODE:
            focusNextListItem(UP_ARROW_KEY_CODE);
            return;

        case ESCAPE_KEY_CODE:
            closeSelectList(e);
            return;

        default:
            return;
    }
});

function closeSelectList(e) {
    let parentSelect = e.target.offsetParent;
    parentSelect.classList.remove('block');
    parentSelect.classList.add('hidden');
}

function openSelectList(e) {
    let parentSelect = e.target.offsetParent;
    parentSelect.classList.remove('hidden');
    parentSelect.classList.add('block');
}

function focusNextListItem(direction) {
    const currentId = e.target.getAttribute('id');
    const activeElementId = document.activeElement.id;
    console.log(listItemIds);
    if (activeElementId === currentId) {
        document.querySelector(`#${currentId}`).focus();
    } else {
        const currentActiveElementIndex = listItemIds.indexOf(activeElementId);
        if (direction === DOWN_ARROW_KEY_CODE) {
            const currentActiveElementIsNotLastItem =
                currentActiveElementIndex < listItemIds.length - 1;
            if (currentActiveElementIsNotLastItem) {
                const nextListItemId = listItemIds[currentActiveElementIndex + 1];
                console.log(nextListItemId);
                document.querySelector(`#${nextListItemId}`).focus();
            }
        } else if (direction === UP_ARROW_KEY_CODE) {
            const currentActiveElementIsNotFirstItem =
                currentActiveElementIndex > 0;
            if (currentActiveElementIsNotFirstItem) {
                const nextListItemId = listItemIds[currentActiveElementIndex - 1];
                console.log(nextListItemId);
                document.querySelector(`#${nextListItemId}`).focus();
            }
        }
    }
}

function setSelectedListItem(e) {
    let currentValue = e.target.getAttribute('data-value');
    let currentLabel = e.target.innerHTML;
    let parentSelectButton = e.target.offsetParent.parentElement.children[0]
    let parentSelectLabel = e.target.offsetParent.parentElement.children[0].children[0];

    // assign values
    parentSelectButton.value = currentValue;
    parentSelectLabel.innerHTML = currentLabel;
}


// new/existing client radio button 
$('input[name="client_type"]').click(function(){

    if( $('#new_client').is(':checked') ){
        console.log('new client');
        $('.new-client').addClass('active');
        $('.new-client-hide').hide();
    }else{
        console.log('old client');
        $('.new-client').removeClass('active');
        $('.new-client-hide').show();
    }
});

$('#tooltip-existing-client').hover(function(){
    console.log('hovered')
    $('.tooltip-registration').toggle();
});